.feedback {
  background-color: $blue;
  padding-bottom: 113px;

  background-image: url("../img/background-feedback2.png");
  background-repeat: no-repeat;
  background-position: 196px 93px;
}

.feedback__container {
  display: flex;
}

.feedback__form-block {
  width: 60%;
  display: flex;
  flex-direction: column;
}

.feedback-image {
  display: block;
  margin-right: 25px;
  margin-left: auto;
  margin-top: 161px;
}

.feedback__image-container {
  width: 50%;
}

.feedback__inputs-block {
  width: 96%;
  display: flex;
  justify-content: space-between;
}

.feedback__button-block {
  width: 96%;
  display: flex;
}

.feedback__title {
  width: 85%;
  font-family: $RobotoCondensed;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;

  color: $white;

  margin: 0;
  margin-left: 11%;

  padding-top: 91px;
}

.feedback__subtitle {
  width: 317px;
  font-family: $PTMono;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;

  color: $white;

  margin: 0;
  margin-left: 11.6%;

  padding-top: 10px;
}

.feedback__form {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  width: 77%;

  padding-top: 51px;
  margin-right: auto;
  margin-left: auto;
}

.feedback__form p {
  margin: 0;
}

.feedback__form input[type="text"],
.feedback__form input[type="tel"] {
  font-family: $PTMono;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  padding-left: 16px;
  padding-top: 13px;
  padding-bottom: 15px;

  border: none;
  outline: none;
  color: $white;
  background-color: rgba(25, 186, 255, 0.9);
  margin-bottom: 19px;
  -webkit-border-radius: 0;

  &:focus {
    outline: 1px solid $white;
  }
}

.feedback__form input[type="text"] {
  width: 44%;
}

.feedback__form input[type="tel"] {
  width: 44%;
}

.feedback__form input::placeholder,
.feedback__form textarea::placeholder {
  font-family: $PTMono;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: $white;
}

.feedback__form textarea {
  width: 93%;
  height: 140px;

  font-family: $PTMono;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  padding-left: 16px;
  padding-top: 13px;

  border: none;
  outline: none;
  color: $white;
  background-color: rgba(25, 186, 255, 0.9);

  margin-top: 19px;
  resize: none;

  &:focus {
    outline: 1px solid $white;
  }
}

.feedback__form button[type="submit"] {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 47%;
  min-height: 45px;

  font-family: $RobotoCondensed;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  border: 1px solid $white;
  box-sizing: border-box;
  border-radius: 5px;
  outline: none;
  background-color: transparent;

  color: $white;
  cursor: pointer;

  margin-top: 44px;

  &:hover,
  &:focus {
    color: $blue;
    background-color: $white;
  }

  &:active {
    opacity: 0.8;
  }
}

.feedback__checkbox {
  position: absolute;
  top: 350px;
  left: 312px;
}

.feedback__label {
  width: 40%;
  font-family: $PTMono;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;

  color: $white;

  opacity: 0.8;
  margin-top: 51px;
  margin-left: 70px;
}

.feedback__checkbox + .feedback__label {
  position: relative;
  cursor: pointer;
}

.feedback__label::before {
  content: "";
  position: absolute;
  left: -32px;
  top: 1px;
  display: block;
  width: 16px;
  height: 16px;
  box-sizing: border-box;
  border: 1px solid $white;
}

.feedback__checkbox:checked + .feedback__label::before {
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 7.5px 4.5px;
}

@media (max-width: $tablet) {
  .feedback {
    padding-bottom: 106px;
    background-image: none;
  }

  .feedback__form-block {
    width: 100%;
    display: flex;
    flex-direction: column;
  }

  .feedback__image-container {
    display: none;
  }

  .feedback__container {
    align-items: flex-start;
  }

  .feedback__title {
    width: 565px;
    font-size: 32px;
    line-height: 45px;

    margin-left: 68px;

    padding-top: 80px;
  }

  .feedback__subtitle {
    width: 328px;
    margin-left: 72px;

    padding-top: 16px;
  }

  .feedback__form {
    width: 95%;

    padding-top: 54px;
    margin: 0;
  }

  .feedback__form input[type="text"] {
    width: 39.5%;
    margin-left: 72px;
    margin-right: 32px;
  }

  .feedback__form input[type="tel"] {
    width: 39.5%;
  }

  .feedback__form textarea {
    width: 83%;
    height: 144px;
    margin-left: 72px;
    margin-top: 23px;
  }

  .feedback__form button[type="submit"] {
    margin-left: 72px;
    margin-top: 45px;
  }

  .feedback__label {
    margin-top: 53px;
    margin-left: 75px;
  }

  .feedback__label::before {
    left: -34px;
  }
}

@media (max-width: $mobile) {
  .feedback {
    padding-bottom: 28px;
    background-image: none;
  }

  .feedback__inputs-block {
    flex-direction: column;
  }

  .feedback__button-block {
    width: 100%;
    flex-direction: column;
  }

  .feedback__title {
    width: 243px;
    font-size: 28px;
    line-height: 39px;

    margin: 0;

    padding-top: 15px;
  }

  .feedback__subtitle {
    width: 269px;
    margin-left: 0;
    padding-top: 11px;
  }

  .feedback__form {
    width: 100%;
    flex-direction: column;

    padding-top: 27px;
    margin: 0;
  }

  .feedback__form input[type="text"] {
    width: 98%;
    margin: 0;
    margin-bottom: 32px;
  }

  .feedback__form input[type="tel"] {
    width: 98%;
    margin: 0;
    margin-bottom: 30px;
  }

  .feedback__form textarea {
    width: 94%;
    height: 90px;
    margin: 0;
  }

  .feedback__form button[type="submit"] {
    width: 100%;
    //width: 200px;
    height: 46px;
    margin: 0;
    margin-top: 31px;
  }

  .feedback__checkbox {
    top: 0;
    left: 0;
  }

  .feedback__label {
    width: 80%;
    margin: 0;
    margin-top: 30px;
    margin-left: 35px;
  }

  .feedback__label::before {
    top: 2px;
    left: -35px;
  }
}
