.advantages {
  background-image: url("../img/background-advantages.png");
  background-size: 1690px 816px;
  background-repeat: no-repeat;
  background-position: 203px 45px;
  padding-bottom: 100px;
  background-color: transparent;
  z-index: -2;
}

.advantages-list {
  @include list-reset();
  display: flex;
  flex-wrap: wrap;
  padding-top: 113px;
  margin-left: 135px;
}

.advantages-list__item {
  width: 44%;
  min-height: 299px;
  border: 1px solid #ebebeb;
  border-left: 3px solid $blue;
  margin-right: 29px;
  margin-bottom: 28px;

  transition: all 300ms ease;
  position: relative;
  background-color: $white;

  &:nth-child(2n) {
    margin-right: 0;
  }
}

.advantages-list__icon {
  fill: $blue;
  padding-top: 38px;
  margin-left: 43px;

  &--watch {
    width: 29px;
    height: 35px;
  }

  &--hired {
    width: 35px;
    height: 35px;
  }

  &--detective {
    width: 35px;
    height: 35px;
  }

  &--price {
    width: 35px;
    height: 35px;
  }
}

.advantages-list__item:hover {
  background-color: $blue;
}

.advantages-list__item:hover h3,
.advantages-list__item:hover p,
.advantages-list__item:hover svg {
  color: white;
  fill: white;
}

.advantages-list__title {
  font-family: $RobotoCondensed;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 34px;
  color: $blue;
  margin: 0;
  margin-left: 41px;
  padding-top: 15px;
}

.advantages-list__description {
  width: 85%;
  font-family: $PTMono;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #666666;
  margin: 0;
  padding-top: 17px;
  padding-bottom: 17px;
  margin-left: 41px;
}

@media (max-width: $tablet) {
  .advantages {
    background-image: url("/img/background-advantages-tablet.png");
    background-size: 559px 873px;
    background-repeat: no-repeat;
    background-position: right 90px;
    padding-bottom: 153px;
    background-color: transparent;
  }

  .advantages-list {
    padding-top: 54px;
    margin-left: 5px;
  }

  .advantages-list__item {
    width: 47.5%;
    min-height: 0;
    border: 1px solid #ebebeb;
    border-left: 2px solid $blue;

    margin-right: 29px;
    margin-bottom: 28px;

    &--watch,
    &--hired {
      min-height: 306px;
    }

    &--detective,
    &--price {
      min-height: 232px;
    }
  }

  .advantages-list__icon {
    padding-top: 24px;
    margin-left: 26px;

    &--watch {
      width: 29px;
      height: 35px;
    }

    &--hired {
      width: 31.72px;
      height: 35px;
    }

    &--detective {
      width: 30.31px;
      height: 32.18px;
    }

    &--price {
      width: 32.18px;
      height: 32.18px;
    }
  }

  .advantages-list__title {
    margin: 0;
    margin-left: 25px;
    padding-top: 3px;
  }

  .advantages-list__description {
    width: 85%;
    font-family: $PTMono;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;

    color: #666666;

    margin: 0;
    padding-top: 8px;
    padding-bottom: 23px;
    margin-left: 24px;
  }
}

@media (max-width: $mobile) {
  .advantages {
    background-image: url("/img/background-advantages-mobile.png");
    background-size: 320px 873px;
    background-repeat: no-repeat;
    background-position: right 652px;
  }

  .advantages-list {
    flex-direction: column;
    padding-top: 21px;
    margin-left: -1px;
  }

  .advantages-list__item {
    width: 99%;
    margin: 0;
    margin-bottom: 19px;

    &--watch,
    &--hired {
      min-height: 310px;
    }

    &--detective {
      min-height: 232px;
    }

    &--price {
      min-height: 244px;
    }
  }

  .advantages-list__title {
    width: 85%;
    font-size: 20px;
    line-height: 28px;

    margin: 0;
    margin-left: 24px;
    padding-top: 8px;
  }

  .advantages-list__description {
    width: 87%;

    &--watch {
      width: 85%;
    }

    &--price {
      width: 85%;
    }
  }
}
